export default {
    name: "StartEndPoint",
    data() {
        return {
            animation: false,
            start: {name: "北京"},
            end: {name: "唐山"},
        }
    },
    methods: {
        toggle() {
            this.animation= true;
            setTimeout(()=>{
                this.animation = false;
            }, 1000)
            let temp = null;
            temp = this.start;
            this.start = this.end;
            this.end = temp;
        },
    }
}
